<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6"><div class="grid-content bg-primary">
        <div class="mini-stat clearfix bg-primary">
          <span class="mini-stat-icon"><i class="mdi mdi-cart-outline"></i></span>
          <div class="mini-stat-info text-right text-white">
            <span class="counter">15852</span>
          </div>
        </div>

        <!---->
      </div></el-col>
      <el-col :span="6"><div class="grid-content bg-primary"></div></el-col>
      <el-col :span="6"><div class="grid-content bg-primary"></div></el-col>
      <el-col :span="6"><div class="grid-content bg-primary"></div></el-col>
    </el-row>
    <div class="content">
      <div id="main"></div>
    </div>
  </div>
</template>
<script>
  //  import theme-name from theme-folder

  // 
  // echarts
  export default {
    data() {
      return {
        // 
        chart: null,
        // 
        opinion: ['', '', '', '', ''],
        opinionData: [{
          value: 26,
          name: ''
        }, {
          value: 31,
          name: ''
        }, {
          value: 18,
          name: ''
        }, {
          value: 28,
          name: ''
        }, {
          value: 21,
          name: ''
        }]
      }
    },
    methods: {
      // 
      drawGraph(id) {
        // 
        this.chart = this.$echarts.init(document.getElementById(id))
        // 
        this.chart.showLoading()
        // data
        var that = this

        // set
        this.chart.setOption({
          title: {
            text: '',
            subtext: '',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            x: 'center',
            y: 'bottom',
            data: this.opinion // this
          },
          toolbox: {
            show: true,
            feature: {
              mark: {
                show: true
              },
              dataView: {
                show: true,
                readOnly: false
              },
              magicType: {
                show: true,
                type: ['pie']
              },
              restore: {
                show: true
              },
              saveAsImage: {
                show: true
              }
            }
          },
          calculable: true,
          series: [{
            name: '',
            type: 'pie',
            // 
            radius: [30, 100],
            // 
            center: ['50%', '50%'],
            roseType: 'area',
            data: this.opinionData, // this
          }]
        })
        this.chart.hideLoading()
      }
    },
    // keypoint
    //  DOM  DOM 
    mounted() {
      this.$nextTick(function() {
        this.drawGraph('main')
      })
    }
  }
</script>

<style scoped>
  .el-row {
    margin-bottom: 20px;
  &:last-child {
     margin-bottom: 0;
   }
  }
  .el-col {
    border-radius: 4px;
  }

  .bg-primary {
    background-color: #67a8e4 !important;
  }

  .grid-content {
    /*min-height: 36px;*/
    border: 1px solid rgba(112, 112, 112, 0.12);
    padding: 20px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    margin-bottom: 20px;
  }

  /* ==============
  Widgets
===================*/
  .widget-chart li {
    width: 31.5%;
    display: inline-block;
    padding: 0;

  i {
    font-size: 22px;
  }
  }

  .mini-stat {
    border: 1px solid rgba($muted,0.12);
    padding: 20px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    margin-bottom: 20px;
  }

  .mini-stat-icon {
    width: 60px;
    height: 60px;
    display: inline-block;
    line-height: 60px;
    text-align: center;
    font-size: 30px;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    border-radius: 100%;
    float: left;
    margin-right: 10px;
    color: $white;
    background-color: rgba($white,0.1);
  }

  .mini-stat-info {
    font-size: 14px;
    padding-top: 2px;
  }

  .mini-stat-info span {
    display: block;
    font-size: 24px;
    font-weight: 600;
  }

  .text-white {
    color: #ffffff !important;
  }

  .mdi {
    display: inline-block;
    font: normal normal normal 24px/1 "Material Design Icons";
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transform: translate(0, 0);
  }

  .mdi-cart-outline:before {
    content: "\F111";
  }


  .content {
    /**/
  }


  #main {
    /*px*/
    height: 400px;
  }
</style>



